
import { Box, Typography, styled, Divider } from '@mui/material';
import { emptychatImg } from '../../../constants/data';

const Component = styled(Box)`
    background: #f8f9fa;
    padding: 30px 0;
    text-align: center;
    height: 100vh;

`
const Container = styled(Box)`
    padding: 0 200px;
`
const Image = styled('img')({
    width:'50%',
    height: '50%',
    marginTop: '100px'
})

const Title = styled(Typography)`
     font-size: 32px;
     margin: 25px 0 10px 0;
     font-family: inherit;
     font-weight: 300px;
     color: #41525d;
`
const SubTitle = styled(Typography)`
     font-size: 14px;
     color: #667781;
     font-weight: 400px;
     font-family: inherit;

     
`
const StyledDivider = styled(Divider)`
    margin: 40px 0;
    opacity: 0.4;
`

const EmptyChat = () => {

    return(
         <Component>
            <Container>
                <Image src={emptychatImg} alt="EmptyChat" />
                <Title>WhatsNew Web</Title>
                <SubTitle>Now send and receive messages.</SubTitle>
                <SubTitle>Use WhatsNew on upto 4 linked devices and 1 phone at the same time.</SubTitle>
               <StyledDivider/>
            </Container>
         </Component>
    )
}

export default EmptyChat