import { useContext } from 'react';
import {Dialog, Box, Typography, List, ListItem, styled} from '@mui/material';
import  qr from './qr.png';
import {GoogleLogin} from '@react-oauth/google'
import jwt_decode from 'jwt-decode'
import { AccountContext } from './../../context/AccountProvider';
import { addUser } from '../../service/api'
const Component = styled(Box)`
  display: flex;
`

const Container = styled(Box)`
  padding: 56px 0px 56px 56px;
`
const QRCode = styled('img')({
    height:264,
    width:264,
    margin:'50px 50px 50px 50px'
})
const Title = styled(Typography)`
   font-size: 26px; 
   color: #525252;
   font-weight: 300;
   font-family: inherit;
   margin-bottom: 25px;
`
const StyledList = styled(List)`
    & > li{
        padding: 0;
        margin-top: 15px;
        font-size: 18px;
        line-height: 28px;
        color: #4a4a4a;
    }
`
const dialogStyle ={
    height:'96%',
    marginTop: '12%',
    width:'60%',
    maxWidth:'100%',
    maxHeight:'100%',
    boxShadow:'none',
    overflow:'hidden',
    backgroundColor:'none'
}

const LoginDialog = () => {
    
    const {setAccount} = useContext(AccountContext)
    const onLoginSuccess = async(res) => {
        //below info must be stored globally that whether the user has logged in or not
        const decoded = jwt_decode(res.credential)
        setAccount(decoded)
        await addUser(decoded)
    }

    const onLoginError = (res) => {
        console.log('Login Failed')
    }

    return(
        
          <Dialog open={true} PaperProps={{sx:dialogStyle}} hideBackdrop={true}>
            
            <Component>
                 <Container>
                    <Title>To use WhatsNew on your PC</Title>
                    <StyledList>
                        <ListItem>1. Open WhatsNew on your PC</ListItem>
                        <ListItem>2. Tap Menu Settings and select WhatsNew Web</ListItem>
                        <ListItem>3. Point your mobile phone to this screen to capture the code</ListItem>
                    </StyledList>
                 </Container>
                 <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                    <QRCode src={qr} alt="QRCode" />

                    <GoogleLogin
                         onSuccess={onLoginSuccess}
                          onError={onLoginError}
                        
                    />
                 </Box>

            </Component>

          </Dialog>
        
    )
}

export default LoginDialog